  background-color: rgba(0,0,0,.25);
  border: 1px solid rgba(0,0,0,.0625);
  border-radius: 3px;
}

.field-dark::-webkit-input-placeholder {
  color: rgba(255,255,255,.75);
}

.field-dark::-moz-placeholder {
